import React, { useState, useEffect, useRef } from 'react';
import { getSpriteSheetPath, getAnimationFrames, getAnimationFrameIndex } from './SpriteManager';
import { getSpriteDataUrl, shouldUseSpriteData } from './SpriteData';
import { getColorForStage } from './SpriteAdapter';

// LilGuy Types
export type LilGuyStage = 'egg' | 'hatchling' | 'normal' | 'angel' | 'devil';
export type LilGuyAnimation = 'idle' | 'walk' | 'happy' | 'sad' | 'angry' | 'shocked' | 'shake' | 'hatch';
export type LilGuyColor = 'green' | 'blue' | 'black';

// Props for the PixelLilGuy Component
export interface PixelLilGuyProps {
  stage?: LilGuyStage;
  animation?: LilGuyAnimation;
  healthLevel?: number; // 0-100, used to determine stage transitions
  size?: number;
  className?: string;
  color?: LilGuyColor;
  onClick?: (event: React.MouseEvent) => void;
  onAnimationComplete?: () => void;
  interactive?: boolean;
}

// Cache for loaded sprite images
interface SpriteCache {
  [key: string]: HTMLImageElement;
}

const spriteCache: SpriteCache = {};

// PixelLilGuy Component
const PixelLilGuy: React.FC<PixelLilGuyProps> = ({
  stage = 'normal',
  animation = 'idle',
  healthLevel = 100,
  size = 64,
  className = '',
  color = 'green',
  onClick,
  onAnimationComplete,
  interactive = true
}) => {
  // Refs and state
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const requestRef = useRef<number>();
  const [currentFrame, setCurrentFrame] = useState(0);
  const [frameCount, setFrameCount] = useState(0);
  const [imageLoaded, setImageLoaded] = useState(false);
  const [useFallback, setUseFallback] = useState(false);
  
  // Determine the correct stage based on health level
  const determineStage = (): LilGuyStage => {
    if (healthLevel <= 0) return 'angel';
    if (healthLevel <= 20) return 'devil';
    if (healthLevel > 90) return 'normal';
    if (healthLevel > 50) return 'hatchling';
    return 'egg';
  };
  
  // Use calculated stage if provided stage is 'auto'
  const actualStage: LilGuyStage = stage === 'normal' ? determineStage() : stage;

  // Determine the right color based on stage
  const actualColor = getColorForStage(actualStage, color);
  
  // Load sprite image and handle caching
  const loadSpriteImage = (src: string): Promise<HTMLImageElement> => {
    return new Promise((resolve, reject) => {
      // Check cache first
      if (spriteCache[src]) {
        resolve(spriteCache[src]);
        return;
      }
      
      const img = new Image();
      img.onload = () => {
        // Cache the loaded image
        spriteCache[src] = img;
        resolve(img);
      };
      img.onerror = (err) => {
        reject(err);
      };
      img.src = src;
    });
  };
  
  // Draw a fallback sprite if image loading fails
  const drawFallbackSprite = () => {
    const canvas = canvasRef.current;
    if (!canvas) return;
    
    const ctx = canvas.getContext('2d');
    if (!ctx) return;
    
    // Check if we should use sprite data
    if (shouldUseSpriteData()) {
      const dataUrl = getSpriteDataUrl(actualColor);
      if (dataUrl) {
        const img = new Image();
        img.onload = () => {
          ctx.clearRect(0, 0, canvas.width, canvas.height);
          ctx.drawImage(img, 0, 0, canvas.width, canvas.height);
        };
        img.src = dataUrl;
      } else {
        // If no data URL, draw a simple shape
        ctx.clearRect(0, 0, canvas.width, canvas.height);
        ctx.fillStyle = actualColor === 'green' ? '#4CAF50' : 
                      actualColor === 'blue' ? '#2196F3' : '#333333';
        ctx.fillRect(canvas.width / 4, canvas.height / 4, canvas.width / 2, canvas.height / 2);
      }
    }
  };
  
  // Animation logic
  const animateLilGuy = () => {
    const canvas = canvasRef.current;
    if (!canvas) return;
    
    const ctx = canvas.getContext('2d');
    if (!ctx) return;
    
    // Get the animation definition
    const animationDef = getAnimationFrames(actualStage, animation);
    
    // Increment frame count for animation timing
    setFrameCount(prev => prev + 1);
    
    // Get the current animation frame based on the frame count
    const frameIndex = getAnimationFrameIndex(animation, frameCount);
    
    // If using fallback, just update the frame and return
    if (useFallback) {
      setCurrentFrame(frameIndex);
      requestRef.current = requestAnimationFrame(animateLilGuy);
      return;
    }
    
    try {
      // Calculate the current sprite path
      const spritePath = getSpriteSheetPath(actualStage, actualColor, animation, frameIndex);
      
      // Load the current frame sprite
      loadSpriteImage(spritePath)
        .then(img => {
          setImageLoaded(true);
          
          // Clear canvas and draw the sprite
          ctx.clearRect(0, 0, canvas.width, canvas.height);
          ctx.drawImage(img, 0, 0, canvas.width, canvas.height);
          
          // Update current frame
          setCurrentFrame(frameIndex);
          
          // Call animation complete callback at the end of animation
          if (frameIndex === animationDef.frames.length - 1 && onAnimationComplete) {
            onAnimationComplete();
          }
        })
        .catch(err => {
          console.error(`Error loading sprite: ${spritePath}`, err);
          setUseFallback(true);
          drawFallbackSprite();
        });
      
      // Continue animation loop
      requestRef.current = requestAnimationFrame(animateLilGuy);
    } catch (error) {
      console.error('Animation error:', error);
      setUseFallback(true);
      drawFallbackSprite();
    }
  };
  
  // Initialize animation when component mounts or props change
  useEffect(() => {
    // Reset state when props change
    setFrameCount(0);
    setCurrentFrame(0);
    setImageLoaded(false);
    setUseFallback(false);
    
    // Start animation loop
    requestRef.current = requestAnimationFrame(animateLilGuy);
    
    // Cleanup animation on unmount
    return () => {
      if (requestRef.current) {
        cancelAnimationFrame(requestRef.current);
      }
    };
  }, [actualStage, animation, actualColor]);
  
  // Handle click events
  const handleClick = (e: React.MouseEvent) => {
    if (interactive && onClick) {
      onClick(e);
    }
  };
  
  // Render the component
  return (
    <canvas
      ref={canvasRef}
      width={size}
      height={size}
      className={`pixel-lilguy ${className}`}
      onClick={handleClick}
      style={{ cursor: interactive ? 'pointer' : 'default' }}
    />
  );
};

export default PixelLilGuy;
